/**
 * 下载图片并返回promise对象
 * @param url 图片的地址
 * @returns {Promise}
 */
export const loadImg = (url) => {
  const promise = new CancelablePromise((resolve, reject) => {
    if (url) {
      const img = new Image();
      img.onload = () => {
        resolve({
          msg: 'done',
          url,
          img
        });
      };

      img.onerror = (ev) => {
        reject({
          msg: ev.message,
          url,
          img
        });
      };
      img.src = url;
    } else {
      reject({
        msg: 'url is not empty',
        url,
        img
      });
    }
  });

  return promise;
};

const createXHR = () => {
  if (window.XMLHttpRequest) {
    return new XMLHttpRequest();
  } else if (window.ActiveXObject) {
    const versions = ['MSXML2.XMLHttp', 'Microsoft.XMLHTTP'];
    for (let i = 0, len = versions.length; i < len; i++) {
      try {
        return new ActiveXObject(version[i]);
        break;
      } catch (e) {}
    }
  } else {
    throw new Error('xhr not support');
  }
};

export const fetchImage = (url) => {
  return new Promise((resolve, reject) => {

    if (RegExp(location.host + '|base64,').test(url)) {
      const im = new window.Image();
      im.onload = () => {
        resolve(im);
      };
      im.onerror = () => {
        reject();
      };
      im.src = url;
    } else {
      const xhr = createXHR();
      xhr.open('GET', url, true);
      xhr.responseType = 'blob';
      xhr.onload = () => {
        const im = new window.Image();

        im.onload = () => {
          resolve(im);
        };

        im.onerror = () => {
          reject();
        };
        im.src = window.URL.createObjectURL(xhr.response);
      };

      xhr.onerror = () => {
        reject();
      };

      xhr.send();
    }
  });
}

export const convertRotateImg = (src, orientation) => {
  return new Promise((resolve, reject) => {
    fetchImage(src)
      .then((img) => {
        const { width, height } = img;
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d');
        const direction = ((orientation + 360) % 360) / 90;
        switch (direction) {
          case 1: {
            canvas.width = height;
            canvas.height = width;
            ctx.rotate(90*Math.PI/180);
            ctx.drawImage(img, 0, -height);
            break;
          }
          case 2: {
            canvas.width = width;
            canvas.height = height;
            ctx.rotate(180*Math.PI/180);
            ctx.drawImage(img, -width, -height);
            break;
          }
          case 3: {
            canvas.width = height;
            canvas.height = width;
            ctx.rotate(270*Math.PI/180);
            ctx.drawImage(img, -width, 0);
            break;
          }
          case 0:
          case 4:
          default: {
            canvas.width = width;
            canvas.height = height;
            ctx.rotate(0);
            ctx.drawImage(img, 0, 0);
            break;
          }
        }
        toObjectUrl(canvas)
          .then(resolve, reject);
      }, reject);
    });
}



